.app {
  max-width: 1440px;
  margin: 0 auto;
  padding: 49px 91px;
  display: flex;
  flex-direction: column;
}

@media (max-width: 1024px) {
  .app {
    padding: 15px 60px;
  }
}

@media (max-width: 635px) {
  .app {
    padding: 22px 12px;
  }
}

@media (max-width: 320px) {
  .app {
    padding: 15px 20px;
  }
}
