@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400&family=Prata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Old+Standard+TT:ital,wght@0,400;0,700;1,400&family=Prata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Old+Standard+TT:ital,wght@0,400;0,700;1,400&family=Prata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Old+Standard+TT:ital,wght@0,400;0,700;1,400&family=Prata&display=swap);
* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  max-width: 1440px;
  margin: 0 auto;
  padding: 49px 91px;
  display: flex;
  flex-direction: column;
}

@media (max-width: 1024px) {
  .app {
    padding: 15px 60px;
  }
}

@media (max-width: 635px) {
  .app {
    padding: 22px 12px;
  }
}

@media (max-width: 320px) {
  .app {
    padding: 15px 20px;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header > img {
  width: auto;
}

.header__search {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
  border-radius: 16px;
  padding: 19px 0px;
}

.header__search > h3 {
  font-family: 'Mulish';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  padding: 0px 16px;
  cursor: pointer;
}

.search__country {
  color: #333333;
}

.search__guest {
  color: #bdbdbd;
}

.header__search > i {
  margin: 0px 16px;
  cursor: pointer;
}

@media (max-width: 635px) {
  .header {
    flex-direction: column;
    align-items: flex-start;
  }

  .header__search {
    margin: 36px;
  }
}

.rooms {
  display: flex;
  flex-direction: column;
  margin-top: 85px;
}

.rooms__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: Montserrat;
  font-style: normal;
}

.rooms__info > h1 {
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #333333;
}

.rooms__info > h3 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #4f4f4f;
}

.rooms__lists {
  display: grid;
  margin-top: 32px;
  grid-template-columns: repeat(3, 32%);
  justify-content: space-between;
}

@media (max-width: 635px) {
  .rooms__lists {
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 1024px) {
  .rooms__lists {
    grid-template-columns: repeat(2, 49%);
  }
}

.room {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 49px;
}

.room > img {
  width: 100%;
  border-radius: 24px;
  height: 269px;
}

.room__detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: Montserrat;
  font-style: normal;
  margin-top: 21px;
}

.room__host {
  display: flex;
  padding: 5px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #4f4f4f;
  border: 1px solid #4f4f4f;
  box-sizing: border-box;
  border-radius: 12px;
}

.room__types {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #828282;
}
.room__types > h5,
.room__types > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
}

.room__rating {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.room__rating > i {
  color: #eb5757;
}

.room__rating > h6 {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #4f4f4f;
}

.room__title {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: #333333;
  margin-top: 21px;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Montserrat;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #a9a9a9;
}

@media (max-width: 635px) {
  .footer {
    font-size: 12px;
    line-height: 14px;
  }
}

.searchModal {
  display: flex;
  flex-direction: column;
  position: fixed;
  background: white;
  top: 0;
  width: 100%;
  z-index: 999;
  padding: 40px 93px;
  opacity: 0.1px;
}

.searchModel__close {
  display: flex;
  margin-bottom: 40px;
}

.searchModel__close > i {
  box-sizing: border-box;
  border-radius: 4px;
  color: white;
  margin-left: 80%;
  cursor: pointer;
  background-color: #eb5757;
  padding: 5px;
}

.searchModal__inputs,
.searchModal__details {
  display: grid;
  grid-template-columns: repeat(3, 426px);
}

.searchModal__location,
.searchModal__beds {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid #333333;
  border-radius: 16px;
  padding: 12px 26px;
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
}

.searchModal__location > span,
.searchModal__beds > span {
  font-family: Mulish;
  font-style: normal;
  font-weight: 800;
  font-size: 9px;
  line-height: 11px;
  text-transform: uppercase;
  color: #333333;
}

.searchModal__location > h5,
.searchModal__beds > h5,
.searchModal__icons > span {
  font-family: Mulish;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #bdbdbd;
}

.searchModal__icons {
  display: flex;
  background: #eb5757;
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1);
  border-radius: 16px;
  color: #f2f2f2;
  padding: 15px 26px;
  width: 90px;
  align-items: center;
}

.searchModal__icons > span {
  color: #f2f2f2 !important;
}

.searchModal__container {
  display: flex;
  flex-direction: column;
}

@media (max-width: 1024px) {
  .searchModal__inputs {
    display: grid;
    grid-template-columns: repeat(3, 300px);
  }
}

@media (max-width: 635px) {
  .searchModal {
    padding: 20px 33px;
  }

  .searchModal__inputs {
    display: flex;
    flex-direction: column;
    width: 300px;
  }

  .searchModal__icons {
    margin-top: 15px;
  }
}

@media (max-width: 320px) {
  .searchModal {
    padding: 20px 23px;
  }

  .searchModal__inputs {
    display: flex;
    flex-direction: column;
    width: 280px;
  }
}

.locations {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.location {
  display: flex;
  padding-top: 42px;
  padding-left: 10px;
  align-items: center;
  cursor: pointer;
}

.location > span {
  font-family: Mulish;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #4f4f4f;
}
.guests {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.guest__info {
  display: flex;
  flex-direction: column;
  padding-top: 42px;
  padding-left: 10px;
  width: 114px;
}

.guest__info > h1 {
  font-family: Mulish;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  color: #333333;
}
.guest__info > h1 > p {
  font-family: Mulish;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  color: #bdbdbd;
}

.guest__number {
  display: flex;
  align-items: center;
  margin-top: 20px;
  justify-content: space-between;
}

.guest__number > i {
  border: 1px solid #828282;
  box-sizing: border-box;
  border-radius: 4px;
}

